@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,600");
* {
  margin: 0;
  padding: 0; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  line-height: 1.6;
  background-color: #374850;
  color: #fff;
  min-height: 100vh; }

.heading-primary {
  color: #fff;
  text-transform: uppercase;
  backface-visibility: hidden;
  margin-bottom: 6rem; }
  .heading-primary--main {
    display: block;
    font-size: 4rem;
    font-weight: 400;
    letter-spacing: 1.75rem; }
  .heading-primary--sub {
    display: block;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 1.2rem; }

.heading-secondary {
  font-size: 3.5rem;
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  background-image: linear-gradient(to right, #7ed56f, #28b485);
  -webkit-background-clip: text;
  color: transparent;
  letter-spacing: .2rem;
  transition: all .2s; }
  .heading-secondary:hover {
    transform: skewY(2deg) skewX(15deg) scale(1.1);
    text-shadow: 0.5rem 1rem 2rem rgba(0, 0, 0, 0.2); }

.heading-tertiary {
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 700; }

.paragraph {
  font-size: 1.6rem; }
  .paragraph:not(:last-child) {
    margin-bottom: 3rem; }

.u-center-text {
  text-align: center !important; }

.u-margin-bottom-big {
  margin-bottom: 8rem !important; }

.u-margin-bottom-medium {
  margin-bottom: 4rem !important; }

.u-margin-bottom-small {
  margin-bottom: 1.5rem !important; }

.u-margin-top-big {
  margin-top: 8rem !important; }

.u-margin-top-huge {
  margin-top: 10rem !important; }

.container {
  width: calc(100% - calc(2 * 4rem));
  max-width: 120rem;
  margin: 0 auto; }
  @media only screen and (max-width: 600px) {
    .container {
      width: calc(100% - calc(2 * 1.5rem)); } }

.page-grid {
  display: grid;
  grid-template-columns: [flush-start] minmax(4rem, 1fr) [container-start] repeat(12, [col-start] minmax(min-content, 10rem) [col-end]) [container-end] minmax(4rem, 1fr) [flush-end];
  grid-template-rows: 65vh; }
  @media only screen and (max-height: 750px) {
    .page-grid {
      grid-template-rows: 100vh; } }
  @media only screen and (max-width: 600px) {
    .page-grid {
      grid-template-columns: [flush-start] minmax(1.5rem, 1fr) [container-start] repeat(12, [col-start] minmax(min-content, 12.5rem) [col-end]) [container-end] minmax(1.5rem, 1fr) [flush-end]; } }

.header {
  position: relative;
  padding: 10rem; }
  .header__intro-box {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center; }

.header__container {
  padding: 3rem; }

.navbar {
  border-top: 8px solid #4497E2;
  height: 7rem;
  font-size: 1.4rem;
  background: transparent;
  border-bottom: #f4f2f2;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .navbar__logo-name {
    height: 3.25rem;
    font-size: 2.5rem;
    font-weight: 800;
    margin-left: 2rem; }
  .navbar__items {
    align-self: stretch;
    display: flex;
    align-items: center; }
    .navbar__items > * {
      padding: 0 2rem;
      cursor: pointer;
      height: 100%;
      display: flex;
      align-items: center; }
    .navbar__items > *:hover {
      background-color: #263238; }

